import React, { useState, useEffect, useCallback } from "react";
import Form from "@rjsf/core";
import validator from "@rjsf/validator-ajv8";

const API_BASE = "/plugins/signalk-data-connector";

// Base schema properties shared between server and client modes
const baseProperties = {
  serverType: {
    type: "string",
    title: "Operation Mode",
    description: "Select Server to receive data, or Client to send data",
    default: "client",
    enum: ["server", "client"],
    enumNames: ["Server Mode - Receive Data", "Client Mode - Send Data"]
  },
  udpPort: {
    type: "number",
    title: "UDP Port",
    description: "UDP port for data transmission (must match on both ends)",
    default: 4446,
    minimum: 1024,
    maximum: 65535
  },
  secretKey: {
    type: "string",
    title: "Encryption Key",
    description: "32-character secret key (must match on both ends)",
    minLength: 32,
    maxLength: 32
  },
  useMsgpack: {
    type: "boolean",
    title: "Use MessagePack",
    description: "Binary serialization for smaller payloads (must match on both ends)",
    default: false
  },
  usePathDictionary: {
    type: "boolean",
    title: "Use Path Dictionary",
    description: "Encode paths as numeric IDs for bandwidth savings (must match on both ends)",
    default: false
  }
};

// Client-only properties
const clientProperties = {
  udpAddress: {
    type: "string",
    title: "Server Address",
    description: "IP address or hostname of the SignalK server",
    default: "127.0.0.1"
  },
  helloMessageSender: {
    type: "integer",
    title: "Heartbeat Interval (seconds)",
    description: "How often to send heartbeat messages",
    default: 60,
    minimum: 10,
    maximum: 3600
  },
  testAddress: {
    type: "string",
    title: "Connectivity Test Address",
    description: "Address to ping for network testing (e.g., 8.8.8.8)",
    default: "127.0.0.1"
  },
  testPort: {
    type: "number",
    title: "Connectivity Test Port",
    description: "Port for connectivity test (80, 443, 53)",
    default: 80,
    minimum: 1,
    maximum: 65535
  },
  pingIntervalTime: {
    type: "number",
    title: "Check Interval (minutes)",
    description: "How often to test network connectivity",
    default: 1,
    minimum: 0.1,
    maximum: 60
  }
};

// Generate schema based on current mode
function getSchema(isClientMode) {
  const properties = { ...baseProperties };
  const required = ["serverType", "udpPort", "secretKey"];

  if (isClientMode) {
    Object.assign(properties, clientProperties);
    required.push("udpAddress", "testAddress", "testPort");
  }

  return {
    type: "object",
    title: "SignalK Data Connector",
    description: "Configure encrypted UDP data transmission between SignalK units",
    required,
    properties
  };
}

// UI Schema for field ordering and styling
const uiSchema = {
  "ui:order": [
    "serverType",
    "udpPort",
    "secretKey",
    "useMsgpack",
    "usePathDictionary",
    "udpAddress",
    "helloMessageSender",
    "testAddress",
    "testPort",
    "pingIntervalTime"
  ],
  secretKey: {
    "ui:widget": "password",
    "ui:help": "Must be exactly 32 characters long"
  },
  serverType: {
    "ui:widget": "select"
  }
};

/**
 * Custom configuration panel for SignalK Data Connector plugin
 * Fetches configuration from API and renders dynamic form
 */
function PluginConfigurationPanel(props) {
  const [formData, setFormData] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [saveStatus, setSaveStatus] = useState(null);

  // Determine if we're in client mode
  const isClientMode = formData.serverType !== "server";

  // Generate schema based on current mode
  const schema = getSchema(isClientMode);

  // Fetch configuration from API on mount
  useEffect(() => {
    async function fetchConfig() {
      try {
        const response = await fetch(`${API_BASE}/plugin-config`);
        if (!response.ok) {
          throw new Error(`Failed to load: ${response.statusText}`);
        }
        const data = await response.json();
        if (data.success && data.configuration) {
          setFormData(data.configuration);
        }
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    }
    fetchConfig();
  }, []);

  // Handle form changes
  const handleChange = useCallback(({ formData: newFormData }) => {
    setFormData(newFormData);
    setSaveStatus(null);
  }, []);

  // Handle form submission
  const handleSubmit = useCallback(async ({ formData: submittedData }) => {
    setSaveStatus({ type: "saving", message: "Saving..." });

    // Clean up client-only fields when in server mode
    const cleanedData = { ...submittedData };
    if (cleanedData.serverType === "server") {
      delete cleanedData.udpAddress;
      delete cleanedData.helloMessageSender;
      delete cleanedData.testAddress;
      delete cleanedData.testPort;
      delete cleanedData.pingIntervalTime;
    }

    try {
      const response = await fetch(`${API_BASE}/plugin-config`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(cleanedData)
      });
      const data = await response.json();

      if (response.ok && data.success) {
        setSaveStatus({
          type: "success",
          message: data.message || "Saved! Restart plugin to apply."
        });
      } else {
        throw new Error(data.error || "Failed to save");
      }
    } catch (err) {
      setSaveStatus({ type: "error", message: err.message });
    }
  }, []);

  if (loading) {
    return <div style={{ padding: "20px", textAlign: "center" }}>Loading configuration...</div>;
  }

  if (error) {
    return (
      <div style={{ padding: "20px", color: "#dc3545" }}>
        <h4>Error Loading Configuration</h4>
        <p>{error}</p>
      </div>
    );
  }

  return (
    <div className="signalk-data-connector-config">
      {saveStatus && (
        <div
          style={{
            padding: "10px 15px",
            marginBottom: "15px",
            borderRadius: "4px",
            backgroundColor:
              saveStatus.type === "success"
                ? "#d4edda"
                : saveStatus.type === "error"
                  ? "#f8d7da"
                  : "#fff3cd",
            color:
              saveStatus.type === "success"
                ? "#155724"
                : saveStatus.type === "error"
                  ? "#721c24"
                  : "#856404",
            border: `1px solid ${
              saveStatus.type === "success"
                ? "#c3e6cb"
                : saveStatus.type === "error"
                  ? "#f5c6cb"
                  : "#ffeeba"
            }`
          }}
        >
          {saveStatus.message}
        </div>
      )}
      <Form
        schema={schema}
        uiSchema={uiSchema}
        formData={formData}
        validator={validator}
        onChange={handleChange}
        onSubmit={handleSubmit}
        liveValidate={false}
      >
        <button type="submit" className="btn btn-primary">
          Save Configuration
        </button>
      </Form>
      <style>{`
        .signalk-data-connector-config {
          width: 100%;
        }
        .signalk-data-connector-config .form-group {
          margin-bottom: 1rem;
        }
        .signalk-data-connector-config label {
          font-weight: 600;
          margin-bottom: 0.25rem;
          display: block;
        }
        .signalk-data-connector-config .help-block,
        .signalk-data-connector-config .field-description {
          font-size: 0.85rem;
          color: #666;
          margin-top: 0.25rem;
        }
        .signalk-data-connector-config input,
        .signalk-data-connector-config select {
          width: 100%;
          padding: 0.5rem;
          border: 1px solid #ccc;
          border-radius: 4px;
          font-size: 1rem;
        }
        .signalk-data-connector-config input[type="checkbox"] {
          width: auto;
        }
        .signalk-data-connector-config .btn-primary {
          background-color: #007bff;
          border-color: #007bff;
          color: white;
          padding: 0.5rem 1rem;
          border-radius: 4px;
          cursor: pointer;
          font-size: 1rem;
          margin-top: 1rem;
        }
        .signalk-data-connector-config .btn-primary:hover {
          background-color: #0069d9;
          border-color: #0062cc;
        }
        .signalk-data-connector-config .text-danger {
          color: #dc3545;
          font-size: 0.85rem;
        }
      `}</style>
    </div>
  );
}

export default PluginConfigurationPanel;
